<template>
  <div>
    <div class="content" v-if="profile">
      <div class="left">
        <img
            class="profile-picture"
            :src="profilePicSrc"
            alt="profile picture"
        />

        <div v-if="profile && profile.description !== ''" class="profile-info">
          <div class="header-and-spacer">
            <p>Something about me</p>
            <div class="spacer"></div>
          </div>
          <p>{{ profile.description }}</p>
        </div>

        <div
            v-if="profile && profile.labels && profile.labels.filter(item => item.type === 'GENRE') && profile.labels.filter(item => item.type === 'GENRE').length > 0"
            class="profile-info"
        >
          <div class="header-and-spacer">
            <p>Genres</p>
            <div class="spacer"></div>
          </div>
          <div class="labels">
            <p v-for="genre in profile.labels.filter(item => item.type === 'GENRE' && item.description === null)" :key="genre.name">
              {{ genre.name }}</p>
          </div>
        </div>

        <div v-if="profile && profile.labels && profile.labels.filter(item => item.type === 'INSTRUMENT') && profile.labels.filter(item => item.type === 'INSTRUMENT').length > 0"
            class="profile-info">
          <div class="header-and-spacer">
            <p>Instruments</p>
            <div class="spacer"></div>
          </div>
          <div class="labels">
            <p v-for="instrument in profile.labels.filter(item => item.type === 'INSTRUMENT' && item.description === null)" :key="instrument.name">
              {{ instrument.name }}</p>
          </div>
        </div>
      </div>
      <div class="right">
        <div>
          <h1>{{ profile.firstName }} {{ profile.lastName }}</h1>
          <h3 class="gradient-text">{{ profile.accountType }}</h3>
          <div class="statistics">
            <div class="stat">
              <h4>Samples</h4>
              <span v-if="profile && profile.samples">{{ profile.samples.length }}</span>
            </div>
            <div class="stat">
              <h4>Likes</h4>
              <span v-if="profile && profile.samples">{{
                  profile.samples.reduce((accumulator, sample) => {
                    return accumulator + sample.likes
                  }, 0)
                }}</span>
            </div>
            <div class="stat" v-if="false">
              <h4>Views</h4>
              <span v-if="profile && profile.samples">{{
                  profile.samples.reduce((accumulator, sample) => {
                    return accumulator + sample.views
                  }, 0)
                }}</span>
            </div>
          </div>
          <div class="profile-buttons"
              v-if="isLoggedIn && this.$route.params.id !== String(this.currentUser.user.idUser)">
            <button class="primary-button">Send request</button>
            <button class="secondary-button"><span class="gradient-text">Contact</span></button>
            <a>Report user</a>
          </div>
          <div class="samples">
            <div class="header-and-spacer">
              <p>Samples</p>
              <div class="spacer"></div>
            </div>

            <v-btn @click="openUploadModal" v-if="isLoggedIn && this.$route.params.id === String(this.currentUser.user.idUser)" class="mb-2">Upload</v-btn>

            <div v-if="profile.samples && profile.samples.length > 0">
              <div v-for="sample in profile.samples" class="sample" :key="sample.title">
                <button class="play-btn" @click="togglePlayPause">
                  <span class="material-icons gradient-text">play_arrow</span>
                </button>
                <audio v-bind:src="sample.src" controls controlsList="nodownload">
                </audio>
                <strong>{{ sample.title }}</strong>
                <p>Genre: <strong>{{ sample.genre }}</strong></p>
              </div>
            </div>
            <p v-if="profile.samples && profile.samples.length === 0">
              This artist hasn't uploaded any samples yet.
            </p>
          </div>
        </div>
      </div>

      <button class="edit-profile-btn primary-button" @click="editProfile = !editProfile"
              v-if="isLoggedIn && this.$route.params.id === String(this.currentUser.user.idUser)">
        <span class="material-icons">edit</span>
      </button>
      <div class="edit-profile-modal-container" v-if="editProfile">
        <div class="edit-profile-modal">
          <h3>Edit profile</h3>
          <span id="close-edit-profile-modal" class="material-icons" @click="editProfile = false">close</span>
          <v-form @submit.prevent="updateUserProfile">

            <input v-model="updateParams.firstName" placeholder="First Name" type="text"/>
            <input v-model="updateParams.lastName" placeholder="Laste Name" type="text"/>
            <v-autocomplete
                clearable
                chips
                multiple
                label="Genres"
                v-model="updateParams.genres"
                :items="options.genres"
                item-title="name"
                item-value="id"
            ></v-autocomplete>
            <v-autocomplete
                clearable
                chips
                multiple
                label="Instruments"
                v-model="updateParams.instruments"
                :items="options.instruments"
                item-title="name"
                item-value="id"
            ></v-autocomplete>

            <button type="submit" class="primary-button"><span class="material-icons">save</span>Save changes</button>
          </v-form>
        </div>
      </div>
    </div>
    <div class="content" v-if="!profile">
      <div class="profile-not-found">
        <h2>Profile not found</h2>
        <p>The profile you are looking for doesn't exist (anymore).</p>
        <router-link to="/">
          <button class="secondary-button"><span class="gradient-text">Take me back home</span></button>
        </router-link>
      </div>
    </div>

    <v-dialog v-model="modal.upload.display" width="1200">
      <v-card>
        <v-card-title>
            Upload music samples
        </v-card-title>

        <v-card-text>
            Upload your music samples here. You can upload multiple samples at once.
        </v-card-text>

        <VueFileAgent
          v-model="modal.upload.selectedFiles"
          :deletable="true"
          :multiple="true"
          accept="audio/*"
          @select="onFileSelect($event)"
          @beforedelete="onFileDelete($event)"
        ></VueFileAgent>

        <table v-if="modal.upload.files.length" class="uploadTable mt-3">
          <thead>
            <tr>
              <th>Name</th>
              <th>Description</th>
              <th>BPM</th>
              <th>Genres</th>
              <th>Instruments</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(file, index) in modal.upload.files" :key="index">
              <td>
                <v-text-field 
                  v-model="file.name"
                ></v-text-field>
              </td>
              <td>
                <v-text-field
                  v-model="file.description"
                ></v-text-field>
              </td>
              <td>
                <v-text-field
                  v-model="file.bpm"
                ></v-text-field>
              </td>
              <td>
                <v-autocomplete
                  clearable
                  chips
                  multiple
                  v-model="file.genres"
                  :items="options.genres"
                  item-title="name"
                  item-value="id"
                ></v-autocomplete>
              </td>
              <td>
                <v-autocomplete
                  clearable
                  chips
                  multiple
                  v-model="file.instruments"
                  :items="options.instruments"
                  item-title="name"
                  item-value="id"
                ></v-autocomplete>
              </td>
            </tr>
          </tbody>
        </table>

        <v-divider></v-divider>

        <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn @click="modal.upload.display = false">
                Cancel
            </v-btn>
            <v-btn @click="uploadFiles" class="bg-gradient" color="gradient" :disabled="modal.upload.saveButtonDisabled">
                Upload
            </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-snackbar v-model="modal.upload.feedback.display" :timeout="1500" :color="modal.upload.feedback.success ? 'success' : 'error'">
        {{modal.upload.feedback.success ? 'Upload successful!' : 'Something went wrong.'}}
    </v-snackbar>
  </div>

</template>
<script>
import axios from "axios";
import {mapGetters, mapState} from "vuex";
import config from "@/config";
import * as meta from 'music-metadata-browser';

async function getUserProfile(idUser) {

  try {
    const response = await axios.get(`${config.apiUrl}/profile?idUser=${idUser}`);
    return response.data;
  } catch (error) {
    console.error('Error fetching user profile:', error);
    return {};
  }
}

function togglePlayPause(event) {
  let audioEl = event.target.parentElement.parentElement.querySelector('audio');
  audioEl.onended = function () {
    event.target.parentElement.querySelector('.material-icons').innerText = 'play_arrow';
  };
  if (audioEl.paused) {
    audioEl.play();
    event.target.parentElement.querySelector('.material-icons').innerText = 'pause';
  } else {
    audioEl.pause();
    event.target.parentElement.querySelector('.material-icons').innerText = 'play_arrow';
  }
}

export default {
  data() {
    return {
      profile: {},
      profilePicSrc: "https://t3.ftcdn.net/jpg/05/16/27/58/360_F_516275801_f3Fsp17x6HQK0xQgDQEELoTuERO4SsWV.jpg",
      editProfile: false,
      updateParams: {
        firstName: '',
        lastName: '',
        genres: [17],
        instruments: [],
        labels: [],
        // Add more fields as needed
      },
      options: {
        genres: [],
        instruments: [],
      },
      currentUserId: null,

      modal: {
        upload: {
          saveButtonDisabled: false,
          display: false,
          selectedFiles: [],
          files: [],
          feedback: {
            display: false,
            success: true,
          }
        }
      }

    };
  },
  name: "Profile",
  computed: {
    ...mapGetters(['isLoggedIn']),
    ...mapState({
      currentUser: 'currentUser',
    }),
  },
  methods: {
    togglePlayPause(event) {
      let audioEl = event.target.parentElement.parentElement.querySelector('audio');
      audioEl.onended = function () {
        event.target.parentElement.querySelector('.material-icons').innerText = "play_arrow"
      };
      if (audioEl.paused) {
        audioEl.play();
        event.target.parentElement.querySelector('.material-icons').innerText = "pause"
      } else {
        audioEl.pause()
        event.target.parentElement.querySelector('.material-icons').innerText = "play_arrow"
      }
    },

    createFileFormData(file){

      const dateNow = new Date().toISOString();

      const musicSample = {
        idUser: this.profile.idUser,
        sampleName: file.name,
        sampleLength: file.length,
        bpm: file.bpm,
        sampleDescription: file.description,
        dateCreated: dateNow,
        dateModified: dateNow,
      };

      // Create form data
      let formData = new FormData();
      formData.append('musicSample', new Blob([JSON.stringify(musicSample)], {
        type: 'application/json'
      }));
      formData.append('file', file.file);

      return formData;

    },

    uploadFiles(){
      console.log('upload files');

      // No Files selected
      if (this.modal.upload.files.length === 0) {
        this.modal.upload.feedback.success = false;
        this.modal.upload.feedback.display = true;
        return;
      }
      
      // Upload files 
      Promise.all(
        this.modal.upload.files.map((file) => this.uploadFileLinkLabels(file))
      )
      .then(() => {
        // File upload successful
        console.log('all files uploaded');
        this.modal.upload.feedback.success = true;
        this.modal.upload.saveButtonDisabled = false;
        this.modal.upload.display = false;
      })
      .catch((err) => {
        // File upload failed
        this.modal.upload.feedback.success = false;
        this.modal.upload.saveButtonDisabled = false;
        console.error('error uploading files', err);
      })
      .finally(() => {
        // Show feedback
        this.modal.upload.feedback.display = true;
      })

    },

    uploadFileLinkLabels(file){
      console.log('upload file, link labels', file);

      return new Promise((success, failure) => {
        new Promise((resolve) => {
          
          // Upload sample
          const fileFormData = this.createFileFormData(file);

          axios
            .post(`${this.$config.apiUrl}/samples/upload`, fileFormData, {
              headers: {
                'Content-Type': 'multipart/form-data'
              }
            })
            .then((response) => {
                const musicSampleId = (response.data != -1) ? response.data : null;
                musicSampleId ? resolve(musicSampleId) : failure();
            })
            .catch((err) => {
                console.error({err});
                failure();
            })

        })
        .then((musicSampleId) => {

          // Link labels to sample
          const labelIds = file.genres.concat(file.instruments);

          const body = {
            musicSampleId,
            labelIds,
          };

          axios
            .post(`${this.$config.apiUrl}/link-labels-to-sample`, body)
            .then((response) => {
                response.data ? success() : failure();
            })
            .catch((err) => {
                console.error({err});
                failure();
            })

        })
      });
    },

    splitNameAndExtension(fileNameAndExtension){
        const nameArr = fileNameAndExtension.split('.');
        const extension = nameArr.pop();
        const name = nameArr.join('.');
        return {name, extension};
    },

    openUploadModal(){
      this.modal.upload.saveButtonDisabled = false;
      this.modal.upload.display = true;
    },

    // Vue file agent

    async onFileSelect(files){
      console.log('file selected', JSON.parse(JSON.stringify(files)));

      const newFilesPromises = files.map(async (file) => {
        const metadata = await meta.parseBlob(file.file);
        const bpm = metadata.common.bpm || null;
        const length = Math.ceil(metadata.format.duration) || null;
        const {name, extension} = this.splitNameAndExtension(file.raw.name);

        return {
          id: file.id,
          file: file.file,
          name,
          extension,
          bpm,
          length,
          description: '',
          genres: [],
          instruments: [],
        }
      });

      const newFiles = await Promise.all(newFilesPromises);
      this.modal.upload.files.push(...newFiles);
    },

    onFileDelete(file){
      console.log('file deleted', JSON.parse(JSON.stringify(file)));

      this.modal.upload.files = this.modal.upload.files.filter((item) => item.id !== file.id);
    },

    async updateUserProfile() {
      try {
        console.log(this.updateParams.genres)

        this.updateParams.labels = this.updateParams.genres.concat(this.updateParams.instruments);
        this.updateParams.labels = JSON.stringify(this.updateParams.labels);


        this.updateParams.genres = JSON.stringify(this.updateParams.genres);
        this.updateParams.instruments = JSON.stringify(this.updateParams.instruments);

        const response = await axios.put(
            `${config.apiUrl}/profile/update/${this.currentUser.user.idUser}`,
            this.updateParams,
            {
              headers: {
                'Content-Type': 'application/x-www-form-urlencoded',
              },
            }
        );

        console.log(response.data); // Log the response if needed

        if (response.status === 200) {
          location.reload();
        }
        // Handle the response as needed in your Vue application
        // For example, show a success message or navigate to another page
      } catch (error) {
        console.error('Error updating profile:', error);
        // Handle errors, show an error message, etc.
      }
    },
    getLabelsOfType(type) {
      return new Promise((resolve, reject) => {

        axios
            .get(`${this.$config.apiUrl}/labels/type/${type}`)
            .then((response) => {
              resolve(
                  response.data.sort((a, b) => a.name.localeCompare(b.name))
              )
            })
            .catch((err) => {
              console.error({err});
              reject()
            })

      });
    },
  },
  async created() {
    // Fetch the current user profile when the component is created
    if (this.$route.params.id === localStorage.getItem('currentUserId')) {
      await this.$store.dispatch('fetchCurrentUser');


      this.profile = {
        ...this.currentUser?.user,
        samples: this.currentUser?.samples,
      };

      console.log(this.profile)
    } else {
      const userProfile = await getUserProfile(this.$route.params.id);
      this.profile = {
        ...userProfile?.user,
        samples: userProfile?.samples || [],
      };
    }

    this.options.genres = await this.getLabelsOfType('genre');
    this.options.instruments = await this.getLabelsOfType('instrument');
    this.updateParams.genres = this.profile.genres;
    this.currentUserId = localStorage.getItem('currentUserId');
  },
};

</script>


<style lang="scss" scoped>
@import "../assets/styles/global.scss";


.content {
  padding: 20px;
  display: flex;
  max-width: 1400px;
  gap: 40px;
  margin-inline: auto;

  .profile-info {
    padding-block: 20px;

    .labels {
      display: flex;
      gap: 5px;
      flex-wrap: wrap;

      p {
        background: $background-2;
        padding: 5px 12px;
        border-radius: 1000px;
      }
    }
  }

  .header-and-spacer {
    display: flex;
    white-space: nowrap;
    align-items: center;
    gap: 5px;
    height: min-content;
    margin-bottom: 10px;

    .spacer {
      width: 100%;
      height: 1px;
      background: $text-primary;
      min-width: 20px;
    }
  }

  .left {
    width: 35%;
    min-width: 35%;
    overflow-x: hidden;

    .profile-picture {
      width: 100%;
      border-radius: 24px;
    }
  }

  .right {
    width: 100%;

    .statistics {
      display: flex;
      gap: 20px;
      padding-block: 20px;

      .stat {
        h4 {
          font-weight: 300;
        }

        span {
          font-weight: bold;
          font-size: 1.5em;
        }
      }
    }

    .profile-buttons {
      align-items: center;
      display: inline-flex;
      gap: 35px;
      //padding: 10px;
      position: relative;

    }

    .samples {
      display: flex;
      flex-direction: column;
      gap: 5px;
      padding-block: 20px;

      .sample {
        display: flex;
        width: 100%;
        background: $background-2;
        border-radius: 12px;
        align-items: center;
        padding: 20px;
        gap: 20px;

        audio {
          filter: invert(1);

          &::-webkit-media-controls-panel {
            background: #e5e5e5;
          }

          &::-webkit-media-controls-play-button {
            display: none;
          }
        }

        .play-btn {
          background: $background-3;
          height: 50px;
          width: 50px;
          border-radius: 100%;
          display: flex;
          justify-content: center;
          align-items: center;
          transition: .2s;

          .material-icons {
            font-size: 30px;
          }

          &:hover {
            cursor: pointer;
            transform: scale(1.1);

            .material-icons {
              font-variation-settings: 'FILL' 1,
              'wght' 400,
              'GRAD' 0,
              'opsz' 24;
            }
          }
        }
      }
    }
  }

  .edit-profile-btn {
    position: fixed;
    right: 20px;
    bottom: 20px;
  }

  .edit-profile-modal-container {
    width: 100%;
    height: 100%;
    position: fixed;
    background: rgb(17, 17, 17, .8);
    display: flex;
    justify-content: center;
    align-items: center;

    .edit-profile-modal {
      margin: auto;
      background: $background-2;
      padding: 20px;
      gap: 20px;
      display: flex;
      flex-direction: column;
      border-radius: 16px;
      position: relative;

      #close-edit-profile-modal {
        position: absolute;
        top: 10px;
        right: 10px;
        padding: 0;
        height: 20px;
        width: 20px;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
      }

      form {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 10px;

        .v-input {
          width: 100%;
        }

        input {
          background: $background-3;
          padding: 10px 20px;
          border-radius: 8px;
          color: $text-primary;
        }
      }

      button {
        margin-top: 20px;
        width: 100%;

        .material-icons {
          margin-right: 10px;
        }
      }
    }
  }

  .profile-not-found {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    a {
      text-decoration: none;
    }
  }
}

.uploadTable{
  width: 100%;

  th{
    text-align: left;
  }
}

@media only screen and (max-width: 920px) {
  .content {
    flex-direction: column;
    padding-bottom: 150px;

    .left {
      order: 2;
      width: 100%;

      .profile-picture {
        max-width: 250px;
      }
    }

    .right {
      order: 0;
    }
  }
}
</style>