<script>
import axios from "axios";
import router from "@/router";
import MusicSample from "@/components/MusicSample";

export default {
  components: { 
    MusicSample 
  },
  data() {
    return {
      dataSearch: null,
      searchBar: window.location.pathname.replace("/search/", "")
    };
  },
  methods: {
    search() {
       return axios
            .get(`${this.$config.apiUrl}/search/samples`, {params: {query: this.searchBar}})
            .then((response) => {
              console.log(response.data)
              this.dataSearch = response.data
            })
            .catch((err) => {
              console.error({err});
            })
    },
    searchFieldOnUpdate(){
      console.log(this.searchBar);
      if (this.searchBar.length > 0){
        router.push({ path: '/search/'+this.searchBar })
        this.search()
      } else {
        router.push({ path: '/' })
      }
    }
  },
  created() {
    setTimeout(() => {
      document.getElementById('searchField').focus()
    }, 10)
  },
  beforeMount() {
    this.search()
  },
};

</script>

<template>
  <div>

    <div class="v-row">
      <div class="v-col-11">
        <div class="v-container">
          <div class="v-row">
            <div class="v-col-12">
              <h1>Explore</h1>
              <v-text-field 
                id="searchField" 
                v-model="searchBar" 
                @keyup="searchFieldOnUpdate($event)" 
                label="Search Sample"
                clearable
                append-inner-icon="mdi-magnify"
              ></v-text-field>

              <p class="line">Results</p>

              <div class="sampleContainer sampleContainer-6">
                <MusicSample
                    v-for="sample of dataSearch" 
                    :key="sample.idMusicSample"
                    :sample="sample"
                ></MusicSample>
              </div>

            </div>
          </div>
        </div>
      </div>
    </div>
  </div>

</template>

<style scoped lang="scss">
@import "../assets/styles/global.scss";

.v-col-12 {
  padding-block: 0;
}

</style>