<template>
  <div>
    <!-- <nav-bar></nav-bar> -->

    <div class="v-row">
      <div class="v-col-11">
        <div class="v-container">
          <div class="v-row">
            <div class="v-col-12">

              <h1>Explore</h1>
              <v-text-field 
                id="searchField" 
                v-model="search" 
                @keyup="searchFieldOnUpdate($event)" 
                label="Search Sample"
                clearable
                append-inner-icon="mdi-magnify"
              ></v-text-field>

              <div v-if="doneLoading">
                <p class="line">Most played sample of the week</p>

                <div class="sampleContainer">
                  <MusicSample
                    :sample="samples.mostPlayedThisWeek"
                    highlight
                  ></MusicSample>
                </div>

                <p class="line">Recommended</p>

                <div class="sampleContainer sampleContainer-6">
                  <MusicSample
                      v-for="sample of samples.recommended" 
                      :key="sample.idMusicSample"
                      :sample="sample"
                  ></MusicSample>
                </div>

                <p class="line">Recently played</p>

                <div class="sampleContainer sampleContainer-6">
                  <MusicSample
                      v-for="sample of samples.recentlyPlayed" 
                      :key="sample.idMusicSample"
                      :sample="sample"
                  ></MusicSample>
                </div>
              </div>

            </div>
          </div>
        </div>
      </div>
    </div>
  </div>

</template>

<script>
import MusicSample from "@/components/MusicSample";
import axios from "axios";
import router from "@/router";

export default {
  components: { 
    MusicSample 
  },
  created() {

    Promise.all([
      this.getMostPlayedThisWeek(),
      this.getRecommended(),
      this.getRecentlyPlayed(),
    ])
    .then(() => {
      this.doneLoading = true;
      
      console.log(
        JSON.parse(JSON.stringify(this.samples))
      );
    })
    .catch((err) => {
      console.error({err});
    });

    
    setTimeout(() => {
      document.getElementById('searchField').focus()
    }, 10);

  },
  data() {
    return {

      search: null,

      samples: {
        mostPlayedThisWeek: null,
        recommended: [],
        recentlyPlayed: []
      },

      doneLoading: false,

    }
  },
  methods: {

    // Api calls

    getMostPlayedThisWeek(){
      return new Promise((resolve, reject) => {
        axios
          .get(`${this.$config.apiUrl}/samples/most_played_this_week`)
          .then((response) => {
              console.log({response});
              this.samples.mostPlayedThisWeek = response.data;
              resolve();
          })
          .catch((err) => {
            console.error({err});
            reject();
          })
      });
    },

    getRecommended(){
      return new Promise((resolve, reject) => {
        axios
          .get(`${this.$config.apiUrl}/samples/recommended`)
          .then((response) => {
              console.log({response});
              this.samples.recommended = response.data;
              resolve();
          })
          .catch((err) => {
            console.error({err});
            reject();
          })
      });
    },

    getRecentlyPlayed(){
      return new Promise((resolve, reject) => {
        axios
          .get(`${this.$config.apiUrl}/samples/recently_played`)
          .then((response) => {
              console.log({response});
              this.samples.recentlyPlayed = response.data;
              resolve();
          })
          .catch((err) => {
            console.error({err});
            reject();
          })
      });
    },

    // Event handlers

    searchFieldOnUpdate(){
      if (this.search) router.push({ path: 'search/'+this.search })
    },

  }
}
</script>

<style lang="scss" scoped>
@import "../assets/styles/global.scss";

.v-col-12{
  padding-block: 0;
}

</style>
