<template>
  <div id="appContainer" :class="showNavBar ? 'hasNavBar' : ''">
    <nav-bar v-if="showNavBar"></nav-bar>
    <router-view class="main" :key="$route.fullPath"></router-view>
  </div>
</template>

<script>
import { computed } from 'vue';
import { useRouter } from 'vue-router';
import NavBar from "@/components/Nav-bar";

export default {
  components: { NavBar },
  setup() {
    const router = useRouter();

    const showNavBar = computed(() => {
      const pathsToHideNavBar = ['/login', '/register'];
      return !pathsToHideNavBar.includes(router.currentRoute.value.path);
    });

    return { showNavBar };
  }
}
</script>

<style>
  @import url('https://fonts.googleapis.com/css2?family=Material+Symbols+Outlined:opsz,wght,FILL,GRAD@20..48,100..700,0..1,-50..200');
</style>

<style lang="scss" scoped>
  #appContainer{
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;

    &.hasNavBar .main{
      padding-top: 20px;
    }
  }
</style>